.tableWrap{
    
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        
}
body{
    background: linear-gradient(139deg,#2c69eccb 0%,#db187ab6 90%);
}

table th,table td{
    border:1px solid black ;
    font-size: large;
    padding: 10px;
}
table{
    border-collapse: collapse;
    align-content: center;
    table-layout: auto;
    max-width: fit-content;
    border: #2c69eccb;
}
label{
    font-size: larger;
    font-weight: bold;
}
input{
    font-size: medium;
    font-weight:bold ;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
    align-content: center;
}
.addform button{
    background-color: rgb(95, 19, 61);
    padding: 10px;
    cursor: pointer;
    color: aliceblue;
    font-weight: bolder;
    margin-left: 60px;
    margin-bottom: 10px;
    text-align: center;
}
